import { AxiosError, AxiosResponse } from 'axios';

/**
 * Allowed HTTP Verbs for outgoing HTTP calls
 */
export type HTTPMethod = 'get' | 'head' | 'post' | 'put' | 'patch' | 'delete' | 'options' | undefined;

/**
 * Options that can be set for HTTP requests
 */
export interface IHTTPRequestOptions {
    headers?: { [index: string]: string };
    timeout?: number;
}

/**
 * Interface containing metadata about OData responses.
 * Value field is only populated if a collection is being returned
 */
export interface IODataResponse<T> {
    '@odata.context': string;
    value?: T;
}

// tslint:disable-next-line
export interface IHTTPResponse<T = {}> extends AxiosResponse<T> {}

// tslint:disable-next-line
export interface IHTTPError extends AxiosError {}
